@import '../../styles/utils/utils.scss';

.no-underline {
  text-decoration: none;
  color: inherit;
}

.card {
  display: flex;

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border: 1px solid $color-elements;
    box-sizing: border-box;
    padding: 32px;
    width: 215px;
    height: 640px;

    &:hover {
      opacity: 0.8;
    }
    
    @include tablet {
      width: 240px;
    }

    @include desktop { 
      width: 275px;
    }
  }


  &__img {
    display: flex;
    flex-direction: column;
    width: 100%;
    flex-grow: 1;

    &-photo {
      width: 100%;
    }
  }

  &__title {
    color: $color-primary;
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    margin-top: 16px;
  }

  &__price {
    margin-top: 8px;
    display: flex;

    &-head {
      margin: 0 8px 0 0;

      &::before {
        content: "$";
      }
    }

    &-discounted {
      margin: 0;
      text-decoration: line-through;
      color: $color-secondary;
      font-weight: 500;

      &::before {
        content: "$";
      }
    }
  }

  &__divider {
    margin-top: 8px;
    height: 1px;
    background-color: $color-elements;
  }

  &__info {
    display: flex;
    justify-content: space-between;
    list-style: none;
    padding: 0;
    margin: 16px 0 16px 0;

    &-left {
      display: flex;
      flex-direction: column;
      gap: 8px;
      font-size: 12px;
      font-weight: 700;
      color: $color-secondary;
    }

    &-right {
      display: flex;
      flex-direction: column;
      gap: 8px;
      font-size: 12px;
      font-weight: 700;
      color: $color-primary;
      text-align: right;
    }
  }

